
import { computed } from "vue";
import { Options, Vue } from "vue-class-component";
import { useStore } from "vuex";
import draggable from "vuedraggable";

declare var M: any;

@Options({
  components: {
    draggable,
  },
  // watch: {
  //   todos(arr) {
  //     this.todos2 = arr;
  //   },
  // },
})
export default class Home extends Vue {
  drag = false;

  todos: any = [];
  // todos2: any = [];
  todo = "";
  todoToUpdate = {};

  isEdit = -1;

  store = useStore();
  log() {
    
    this.store.commit("UPDATE_ALL_TODO", this.todos);
    this.showToast("Item moved");
  }
  save() {
    this.store.commit("ADD_TODO", [{ todo: this.todo, check: false }]);
    this.todo = "";
    this.showToast("Created");
  }
  check(id: any) {
    if (this.isEdit === -1) {
      this.store.commit("CHECK_TODO", id);
      this.showToast("Updated");
    }
  }
  deleteTodo(id: any) {
    this.store.commit("DELETE_TODO", id);
    this.showToast("Deleted");
  }
  updateTodo(id: number) {
    this.store.commit("UPDATE_TODO", {
      todo: this.todoToUpdate,
      id,
    });
    this.isEdit = -1;
    this.showToast("Updated");
    this.todoToUpdate = {};
  }
  created() {
    this.store.dispatch("GET_TODOS");
    // this.todos = JSON.parse(JSON.stringify(this.store.state.todos));
    // this.todos2 = JSON.parse(
    //   JSON.stringify(
    //     computed(() => {
    //       // this.todos2 = this.store.state.todos;
    //       return this.store.state.todos;
    //     })
    //   )
    // );

  }

  mounted() {
    this.todos = JSON.parse(JSON.stringify(this.store.state.todos));
  }

  showToast(msg: string) {
    M.toast({ html: msg });
  }
}
